import React, {useState} from 'react';
import useCountdownTimer from '../../hooks/useCountdownTimer'
import classes from './CountdownTimer.module.css'
import TimerSettings from "./TimerSettings";

const CountdownTimer = () => {
	const onTimerElapsed = () => { };

	const [showSettings, setShowSettings] = useState(false);
	const [settings, setSettings] = useState({
		countdown: 15
	});

	const timer = useCountdownTimer({
		totalSeconds: settings.countdown,
		timerElapsed : onTimerElapsed
	});

	const formatTimeFragment = (time => { return time.toString().padStart(2, '0') });
	const formatTime = (time =>
		<React.Fragment>
			<label>{formatTimeFragment(time.hours)}</label>
			<label>:</label>
			<label>{formatTimeFragment(time.minutes)}</label>
			<label>:</label>
			<label>{formatTimeFragment(time.seconds)}</label>
		</React.Fragment>
	);

	const onStart = () => {
		timer.start();
	};

	const onStop = () => {
		timer.stop();
	};

	const onSettings = () => {
		setShowSettings(true);
	};

	const hideSettingsHandler = () => {
		setShowSettings(false);
	};

	const saveSettingsHandler = (updatedSettings) => {
		setShowSettings(false);
		setSettings(updatedSettings);
		timer.reset(updatedSettings.countdown);
	};

	return (
		<div>
			{ showSettings && <TimerSettings settings={settings} onClose={hideSettingsHandler} onSave={saveSettingsHandler} /> }
			<h1>Countdown Timer</h1>
			<hr />
			<div className={classes.timer}>
				{ formatTime(timer.timeLeft) }
			</div>
			<button type='button' className='btn' onClick={onStart}>Start</button>
			<button type='button' className='btn' onClick={onStop}>Stop</button>
			<button type='button' className={`btn ${classes.settings}`} onClick={onSettings}>Settings</button>
		</div>
	);
};

export default CountdownTimer;