import { useState, useEffect } from 'react';

const useCountdownTimer = (props) => {
	const initialTotalSeconds = props.totalSeconds;

	const getTimeLeft = (totalSec) => {
		return {
			hours: Math.floor((totalSec / 3600) % 60),
			minutes: Math.floor((totalSec / 60) % 60),
			seconds: totalSec % 60
		};
	};

	const [isActive, setIsActive] = useState(false);
	const [totalSeconds, setTotalSeconds] = useState(initialTotalSeconds);

	const reset = (timerValue) => {
		setTotalSeconds(timerValue);
	};

	const start = () => {
		setIsActive(true);
	};

	const stop = () => {
		setIsActive(false);
		setTotalSeconds(initialTotalSeconds);
	};

	useEffect(() => {
		let interval = null;
		if (isActive) {
			interval = setInterval(() => {
				setTotalSeconds(totalSeconds => totalSeconds - 1);

				if (totalSeconds === 1) {
					setIsActive(false);
					props.timerElapsed();
				}
			}, 1000);
		} else if (!isActive && totalSeconds > 0) {
			clearInterval(interval);
		}
		return () => {
			return clearInterval(interval);
		}
	}, [isActive, totalSeconds]);

	return {
		timeLeft: getTimeLeft(totalSeconds),
		start: start,
		stop: stop,
		reset: reset,
		getTimeLeft: getTimeLeft,
		isActive: isActive
	};
};

export default useCountdownTimer;