import React, { useState } from 'react';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';

const TimerSettings = (props) => {
	const [formSettings, setFormSettings] = useState(props.settings);

	const handleChangeHandler = (prop) => (event) => {
		setFormSettings({ ...formSettings, [prop]: event.target.value });
	};

	return (
		<Dialog open={true} onClose={props.onClose}>
			<DialogTitle>Settings</DialogTitle>
			<DialogContent>
				<TextField
					autoFocus
					margin="dense"
					id="countdown"
					label="Countdown (seconds)"
					type="number"
					fullWidth
					variant="standard"
					onChange={handleChangeHandler('countdown')}
					value={formSettings.countdown}
				/>
			</DialogContent>
			<DialogActions>
				<Button onClick={props.onClose}>Cancel</Button>
				<Button onClick={props.onSave.bind(null, formSettings)}>Update</Button>
			</DialogActions>
		</Dialog>
	);
};

export default TimerSettings;