import React, { useState, useEffect } from 'react';
import classes from './StopwatchTimer.module.css';

const StopwatchTimer = () => {
	const [isActive, setIsActive] = useState(false);
	const [seconds, setSeconds] = useState(0);
	const [minutes, setMinutes] = useState(0);
	const [hours, setHours] = useState(0);

	const onStart = () => {
		setIsActive(true);
	};

	const onStop = () => {
		setSeconds(0);
		setMinutes(0);
		setHours(0);
		setIsActive(false);
	};

	useEffect(() => {
		let interval = null;
		if (isActive) {
			interval = setInterval(() => {
				if (seconds === 59) {
					setSeconds(0);
					setMinutes(minutes => minutes + 1);
					if (minutes === 59) {
						setMinutes(0);
						setHours(hours => hours + 1);
					}
				} else {
					setSeconds(seconds => seconds + 1);
				}
			}, 1000);
		} else if (!isActive && seconds !== 0) {
			clearInterval(interval);
		}
		return () => clearInterval(interval);
	}, [isActive, seconds]);

	const formatTime = (time => { return time.toString().padStart(2, '0') });


	return (
		<div>
			<h1>Stopwatch</h1>
			<hr />
			<div className={classes.timer}>
				<label>{ formatTime(hours) }</label>
				<label>:</label>
				<label>{ formatTime(minutes) }</label>
				<label>:</label>
				<label>{ formatTime(seconds) }</label>
			</div>
			<button type='button' className='btn' onClick={onStart}>Start</button>
			<button type='button' className='btn' onClick={onStop}>Stop</button>
		</div>
	);
};

export default StopwatchTimer;